<template>
    <div id="home_child3">
         <div class="content-box">
                <P class="content-box-title"></P>
                <!--  -->
                <div class="trench-btn-box">
                    <div class="trench-btn">
                        <button class="single-trench" :class="{ingleTrenchActive: isCheck}" @click="singCheck"></button>
                        <button class="much-trench" :class="{muchTrenchActive: !isCheck}" @click="muchCheck"></button>
                    </div>
                </div>
                <!--  -->
                <div class="trench-news-box">
                    <div class="trench-select" v-if="!isCheck">
                        <select name="" id="">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                        <span class="add-btn"></span>
                    </div>
                    <div class="trench-list" v-if="!isCheck">
                        <div class="trench-list-open" @click="trenchOpen">{{openList}}
                            <i :class="{openIcon:isOpen}"></i>
                        </div>
                        <ul>
                            <li :class="{trenchItemActive: selectTrenchId == index}" v-for="(item,index) in trenchList" @click="selectTrench(index)">
                                {{item}}
                                <span class="delete-btn"></span>
                            </li>
                        </ul>
                    </div>
                    <div class="trench-msg">
                        <div class="trench-msg-address">
                            <span class="single-trench-name" v-if="isCheck"></span>
                            <label></label>
                            <input class="domain-name" type="text" placeholder="" />
                            <label></label>
                            <el-date-picker v-model="value1" type="datetime" placeholder="">
                            </el-date-picker>
                            <el-date-picker v-model="value2" type="datetime" placeholder="">
                            </el-date-picker>
                            <span class="overTime">
                                <i>*</i>2017-05-042018-03-23</span>
                        </div>
                    </div>
                </div>
                <div class="setHomePage">
                    <!--  -->
                    <div class="phoneImg">
                        <h3></h3>
                        <div class="block-phone-img">
                            <div class="phone-header">
                                <p>{{actName}}</p>
                            </div>
                            <!--  -->
                            <div class="phone-box" v-if="isHome">
                                <div v-if="isKefuOpen" class="kefu"></div>
                                <div class="banner">banner</div>
                                <div class="textarea">{{actTaxt}}</div>
                                <button class="go-active-btn" :class="[{sumberBtn:isSummer},{NewYearBtn:isNewYear}]"></button>
                                <button class="active-rules" :class="[{sumberRuls:isSummer},{NewYearRuls:isNewYear}]"></button>
                            </div>
                            <!-- -->
                            <div class="phone-box" v-if="!isHome">
                                <div v-if="isKefuOpen" class="kefu"></div>
                                <div class="banner phone-banner">banner</div>
                                <div class="two-xiaopiao">
                                    <img src="../../../assets/images/images/demo.png" alt="">
                                    <img src="../../../assets/images/images/upxiaopiao.png" alt="">
                                </div>
                                <div class="textarea phone-textarea">{{upExplain}}</div>
                                <input class="write-phone-nmb" type="text" placeholder="">
                                <button class="go-active-btn conform-btn" :class="[{sumberBtn:isSummer},{NewYearBtn:isNewYear}]"></button>
                                <button class="active-rules" :class="[{sumberRuls:isSummer},{NewYearRuls:isNewYear}]"></button>
                            </div>
                        </div>
                        <div class="tab-btn">
                            <button class="pre-btn"></button>
                            <span>1/4</span>
                            <button class="next-btn"></button>
                        </div>
                    </div>
                    <!--  -->
                    <div class="home-msg">
                        <ul class="home-tab-list">
                            <li :class="{homeTabListAvtive: selectTabId == index}" v-for="(TabItem,index) in tabList" @click="selectTab(index)">{{TabItem}}</li>
                        </ul>
                        <!--  -->
                        <div class="home-configuration" v-if="isHome">
                            <p class="img-title">
                                <span></span>
                            </p>
                            <el-upload class="avatar-uploader" :action="uploadUrl" :show-file-list="false" :on-success="handleSuccess" :data="uploadParam">
                                <img v-if="iconUrl" :src="iconUrl" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                            <p class="img-title"></p>
                            <ul class="style-list">
                                <li :class="{checkedStyle: selectStyleId == index}" v-for="(Sitem,index) in styleList" @click="selectStyle(index)">{{Sitem}}
                                    <i></i>
                                </li>
                            </ul>
                            <p class="img-title"></p>
                            <input class="active-name" maxlength="10" v-model="actName" type="text" placeholder="12">
                            <p class="img-title"></p>
                            <textarea class="active-text" v-model="actTaxt" placeholder="12"></textarea>
                            <p class="img-title"></p>
                            <textarea class="active-text" placeholder="1000"></textarea>
                            <div class="open-kefu">
                                <p class="img-title"></p>
                                <div :class="{off:!isKefuOpen}" @click="OFFKefu"></div>
                            </div>
                            <p v-if="isKefuOpen" class="img-title"></p>
                            <input v-if="isKefuOpen" class="active-name" type="text" placeholder="">
                            <div class="set-next-page"></div>
                        </div>
                        <!-- + -->
                        <div class="home-configuration" v-if="!isHome">
                            <p class="img-title">
                                <span></span>
                            </p>
                            <el-upload class="avatar-uploader" :action="uploadUrl" :show-file-list="false" :on-success="handleSuccess" :data="uploadParam">
                                <img v-if="iconUrl" :src="iconUrl" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                            <p class="img-title"></p>
                            <textarea class="active-text" v-model="upExplain" placeholder="100 "></textarea>
                            <p class="img-title">
                                <span></span>
                            </p>
                            <el-upload class="avatar-uploader" :action="uploadUrl" :show-file-list="false" :on-success="handleSuccess" :data="uploadParam">
                                <img v-if="iconUrl" :src="iconUrl" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                            <div class="open-kefu">
                                <p class="img-title"></p>
                                <div :class="{off:!isOpenRules}" @click="openRulse"></div>
                            </div>
                            <div class="two-set-btn"  v-if="!isOpenRules">
                                <button class="pre-set-btn"></button>
                                <button class="next-set-btn"></button>
                            </div>
                        </div>
                    </div>
                    <div class="clear"></div>
                    <!--  -->
                    <div class="participate-rules" v-if="isOpenRules">
                        <p class="participate-rules-title"></p>
                        <div class="participate-rules-set">
                            <span></span>
                            <label></label>
                            <select name="" id="">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                            <select name="" id="">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                            <label></label>
                            <select name="" id="">
                                <option value="">1</option>
                                <option value="">2</option>
                                <option value=""></option>
                            </select>
                            <label></label>
                            <label></label>
                            <button class="add-reminder-text">+</button>
                            <label></label>
                            <span class="add-btn"></span>
                        </div>
                        <div class="has-rules">
                            <p class="has-rules-title">
                                <span>*</span></p>
                            <ul>
                                <li>1.2 
                                     <button class="delete"></button>
                                </li>
                                <li>1.2 
                                    <button class="delete"></button>
                                </li>
                            </ul>
                            <div class="two-set-btn">
                                <button class="pre-set-btn"></button>
                                <button class="next-set-btn"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!--     -->
            <div class="step-btn">
                <button></button>
                <button class="next-btn"></button>
            </div>
    </div>
</template>
<script>
export default {
   name: 'home-child3',
   components: {

  },
  data(){
    return{
         isCheck: true,
            trenchList: ["1.", "2.", "3.", "1.", "2.", "3.", "1.", "2.", "3.",
                "1.", "2.", "3."
            ],
            selectTrenchId: 0,
            openList: "",
            isOpen: false,
            tabList: ["", "", "", ""],
            isHome: false,
            selectTabId: 0,
            value1: '',
            value2: '',
            uploadUrl: "",
            uploadParam: {},
            iconUrl: "",
            // 
            styleList: ["", "", "", ""],
            selectStyleId: 0,
            // 
            isKefuOpen: true,
            // 
            isSummer: false,
            isNewYear: false,
            // 
            actName: "",
            // 
            actTaxt: "",
            // 
            isOpenRules:false,
            // 
            upExplain:"",
    }
  },
  mounted() {
    //   this.$axios
    //     .get('http://saasmiddle.ugetme.com.cn/activity/app/GetAppTypeList?_=1533103170534')
    //     .then(response => {
    //      console.log(response)
    //     })
    //     .catch(function(error) {
    //       console.log(error);
    //     });
  },
  methods:{
      //  
            singCheck: function () {
                this.isCheck = true;
            },
            //  
            muchCheck: function () {
                this.isCheck = false;
            },
            // 
            selectTrench: function (index) {
                this.selectTrenchId = index;
            },
            // tab
            selectTab: function (index) {
                this.selectTabId = index;
            },
            // 
            selectStyle: function (index) {
                this.selectStyleId = index;
                if (index == 1) {
                    this.isSummer = true;
                    this.isNewYear = false;
                } else if (index == 2) {
                    this.isNewYear = true;
                    this.isSummer = false;
                } else {
                    this.isNewYear = false;
                    this.isSummer = false;
                }
            },
            //  
            trenchOpen: function () {
                this.isOpen = !this.isOpen;
                if (this.isOpen) {
                    this.openList = "";
                } else {
                    this.openList = "";
                }

            },
            // 
            handleSuccess: function () {

            },
            // 
            OFFKefu: function () {
                this.isKefuOpen = !this.isKefuOpen;
            },
            // 
            openRulse: function(){
                this.isOpenRules = !this.isOpenRules;
            }
  }
}
</script>

<style scoped>
@import '../../../assets/css/setPage.css'; /**/
</style>



